import {
	mapGetters,
	mapState
} from 'vuex'

export default {

	data() {
		return {
			sTitleNViewBackground: '',
			iSwiperCurrent: 0,
			iSwiperLength: 0,
			aCarouselList: []
		};
	},
	onLoad() {
		this.loadData();
	},
	computed: {
		...mapState({
			aCateList: state => {
				let aCategories = [];
				if (state.categories) {
					for (let oCategory of state.categories) {
						if (oCategory.level == 2) {
						  aCategories.push(oCategory);
						}
					}
				}
				return aCategories;
			}
		}),
		...mapGetters({
			aProdList: 'products'
		}),
	},
	methods: {
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		async loadData() {
			let aCarouselList = await this.$api.json('carouselList');
			this.sTitleNViewBackground = aCarouselList[0].background;
			this.iSwiperLength = aCarouselList.length;
			this.aCarouselList = aCarouselList;

			//let productsList = await this.$api.json('goodsList');
			//this.productsList = productsList || [];
			await this.$store.dispatch('getCategories');
			await this.$store.dispatch('getProducts');
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.iSwiperCurrent = index;
			this.sTitleNViewBackground = this.aCarouselList[index].background;
		},
		navToProductList(cId) {
			uni.navigateTo({
				url: `/pages/product/list?cId=${cId}`
			})
		},
		//商品详情页
		navToProduct(item) {
			//测试数据没有写id，用title代替
			let id = item.id;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
	},

























}
